.calculator-tab-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6rem;
}

@media (min-width: 992px) {
  .calculator-tab-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: flex-start;
    gap: 6rem;
  }
}

.calculator-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.2rem;
  padding: 1rem;
  border: 1px solid #222;
  flex-shrink: 0;
  flex-grow: 0;
}

.calculator-info {
  color: #933;
  border: 3px solid black;
  border-radius: 100px;
  padding: 12px 35px;
  outline: none;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1;
  text-align: center;
}

.calculator-info :hover {
  color: #50d1c0;
  background-color: #fff;
  text-decoration: none;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
}

.result-section {
  grid-column: 4 span;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  background-color: gray;
  color: white;
  padding: 1rem;
  text-align: right;
  font-size: 2rem;
  margin-bottom: 1rem;
  border: 1px solid #222;
}

.result-section .math {
  border-top: 2px solid #111;
  padding-block: 0.25rem;
  font-size: 1.5rem;
  height: 2rem;
  color: black;
}

.calculator-wrapper .btn {
  display: grid;
  place-content: center;
  padding: 2rem;
  background-color: #323;
  color: #ccc;
  cursor: pointer;
  border: none;
  font-size: 1.5rem;
  transition: background-color 100ms;
}

.calculator-wrapper .btn:hover {
  background-color: #434;
}

.calculator-wrapper .btn:active {
  background-color: #212;
  transform: translateY(0.05rem);
}

.calculator-wrapper .btn-operation {
  background-color: #f5913e;
}

.calculator-wrapper .btn-operation:hover {
  background-color: #ff7756;
}

.calculator-wrapper .btn-operation:active {
  background-color: #411;
}

.btn-0 {
  grid-column: 2 span;
}
